import React from 'react'
import About from './About/About'
import './Body.css'
import Contact from './Contact/Contact'
import Project from './Project/Project'
import Skill from './Skill/Skill'
import Work from './Work/Work'

function Body() {
    return (
        <div className="body">
            <section id="about">
                <About/>
            </section>
            <section id="project">
                <Project/>
            </section>
            <section id="skill">
                <Skill/>
            </section>
            <section id="work">
                <Work/>
            </section>
            <section id="contact">
                <Contact/>
            </section>
        </div>
    )
}

export default Body
