import React from 'react'
import './Mobile.css'

function Mobile({ isOpen, setIsOpen }) {
    return (
        <div className="mobile">
            <div className="close_icon" onClick={() => { setIsOpen(!isOpen) }}>
                <i class="fi-rr-cross-circle"></i>
            </div>
            <div className="mobile_options">
                <div className="mobile_option">
                    <a href="#project">
                        <i class="fi-rr-edit-alt option_icon"></i>Projects
                    </a>
                </div>
                <div className="mobile_option">
                    <a href="#skill">
                        <i class="fi-rr-laptop option_icon"></i>Skills
                    </a>
                </div>
                <div className="mobile_option">
                    <a href="#work">{""}
                        <i class="fi-rr-briefcase option_icon"></i>Work
                    </a>
                </div>
                <div className="mobile_option">
                    <a href="#contact">
                        <i class="fi-rr-following option_icon"></i>Contact
                    </a>
                </div>
            </div>
        </div>
    )
}

export default Mobile
