import React from 'react'
import SocialContact from '../../Common/Social Contact/SocialContact'
import './About.css'
function About() {
    return (
        <div className="about">
            <div className="about_top">
                <div className="about_info">
                    Hello There 🤙, I'm <br /><span className="info_name">
                        Mohammad Rakibuddin.
                    </span><br /> I love experimenting with the web !
                    A self-motivated boy with experience working across the front end of software development. I've built a few projects by myself and I'm looking for a role where I can grow and learn from other experienced team member. Currently pursuing a degree in BTECH. Computer Science from Jamia Hamdard University.
                </div>
                <div className="about_photo">
                    <img src="/images/coding.png" alt="" className="picture"/>
                </div>
            </div>
            <SocialContact/>
        </div>
    )
}

export default About
