import React from 'react'
import './ProjectCard.css'
function ProjectCard({ project }) {
    return (
        <div className="project_card">
            <div className="project_info">
                <label className="project_title">{project.title}</label>
                <div className="project_links">

                    {project.demo && (<a href={project.demo} className="project_link"><div className="link_button">
                        <i class="fi-rr-globe"></i>Demo
                    </div></a>)}

                    {project.github && (
                        <a href={project.github} className="project_link"><div className="link_button">
                            <i class="devicon-github-original-wordmark colored"></i>Github
                        </div></a>
                    )}

                </div>
                <p className="project_about">{project.about}</p>
                <div className="project_tags">
                    {project.tags.map((tag) => {
                        return (
                            <label className="tag">{tag}</label>
                        )
                    })}
                </div>
            </div>
            <img src={project.image} className="project_photo" alt="" />
        </div>
    )
}

export default ProjectCard
