import React from 'react'
import Separator from '../../Common/Separator/Separator'
import { WorkData } from '../../DataFolder/Works'
import './Work.css'
import WorkCard from './WorkCard';

function Work() {
    const data=WorkData;
    return (
        <div className="work">
            <Separator/>    
            <label className="section_title">Work Experience</label>
            <div className="work_list">
                {data.map((item)=>{
                    return(
                        <WorkCard item={item}/>
                    )
                })}
            </div>
        </div>
    )
}

export default Work
