import React,{useState} from 'react'
import './GetContact.css'
import emailjs from 'emailjs-com'
import { toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'

toast.configure()

function GetContact() {
    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [message, setMessage] = useState("");

    const sendEmail =(e)=>{
        e.preventDefault();
        if(name && email && message){

            emailjs.sendForm('service_lcadeqi','template_49th49r',e.target,'user_yjHLsdKxzdycPLFuDzDOU').then(res=>{
                console.log(res);
            }).catch(err=>console.log(err));
            toast.success("Thanks for reaching out! We'll be in Contact Soon 😍")
            setName('');
            setEmail('');
            setMessage('');
        }else if(name||email||message){
            toast.warn("Please fill other details!!!")
        }
        else{
            toast.error("Fill the form Correctly!!!")
        }

    }

    // const notify =()=>{
    //     toast.success("Thanks for reaching out! We'll be in Contact Soon 😍")
    // }

    return (
        <div className="container mt-5 contact_div" >
            <form action="" onSubmit={sendEmail} className="row" style={{margin:"20px 20px 20px 20px"}}>
                <label htmlFor="">Name 🤠</label>
                <input type="text" name="name" className="form-control inputcss" value={name} onChange={(e)=>setName(e.target.value)}  />
        
                <label htmlFor="">Email ✉️ </label>
                <input type="email" name="user_email" className="form-control inputcss" 
                    value={email} onChange={(e)=>setEmail(e.target.value)}
                />

                <label htmlFor="">Message 🖊️ </label>
                <textarea name="message" rows="4" className="form-control inputcss"
                value={message} onChange={(e)=>setMessage(e.target.value)}></textarea>

                <input type="submit" value="Send 🤝" className="form-control btn mt-3 btncss" />
            </form>
        </div>
    )
}

export default GetContact
